
*{
    margin: 0;
    padding: 0;
    list-style: none;
}
body{

}

.nav{
    display: flex;
    text-decoration: none;
    box-shadow: 00px 00px 25px;
    place-items: center;
    justify-content: space-around;
    background-color:rgb(250, 250, 239);
    
}
.branding{
    display: flex;
        justify-content: center;
        align-items:center ;
}
.brand{
    font-size: 25px;
    font-weight: bolder;
    background: -webkit-linear-gradient(green, gold);
    -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
    
    text-decoration: none;
}
.brand span{
    font-size: 40px;
    font-weight: bolder;
}
.links{
    display:flex;
}

.nav li{
    padding: 20px;
    width:100px;
    cursor: pointer;
    display: grid;
    place-items: center;
    
}
.nav li a{
    text-decoration: none;
    font-size: 20px;
    display: inline;
    font-weight: bolder;
    color: black;
   
}

.nav li:hover{
    
    color: white;
    background-color:gray;
}

.nav li:hover a{
    color: white;
    background-color:gray;
    
}

.iccon{
font-size: 25px;
margin-left: 10px;
display: inline;
cursor: pointer;
}

.iccon:nth-child(1){
    color:blue;
}
.iccon:nth-child(2){
    color:red;
}
.iccon:nth-child(3){
    
}
.iccon:nth-child(4){
    color:blue;
}

.nav-ico{
    color: black;
    margin-right: 5px;
    font-size: large;
}

.content{
    position: absolute;
    display: flex;
    justify-content: center;
    place-items: center;
   height: 70vh;
   width: 95vw;
   text-align: center;
}
.mob-icon{
    position: absolute;
    margin: 10px;
    font-size: 20px;
    cursor: pointer;
    display: none;
}
.open{
    color: white;
    background-color: black;
    display:none;
    padding: 5px;
}
.close{
    font-size: 30px;
}

footer{
    position: fixed;
    bottom: 0px;
    left: 0px;
    right: 0px;
    margin-bottom: 0px;
}

footer p {
    position: absolute;
    bottom: 0;
    text-align: center;
    width: 100%;
    padding: 5px;
    background-color:darkslategray;
    color: white;
    font-size: large;
}
footer p a:nth-child(1){
    
       color:white;
   
   }
footer p a:nth-child(2){
 margin-left: 35px;
    color:wheat;

}


@media (max-width:830px)   {
    .nav{
        display:block;
       position: abso;
    }
    .branding{
       
        border-bottom: solid 1px black;
    }
    
    .links{
        display:block;
        border-bottom: solid 1px black;
    }
    
    .nav li{
        display:block;
        width: 100%;   
    }
    .nav li a{
        display: flex;
        justify-content: center;
      
    }
    .icons{
       display: flex;
       justify-content: center;
       padding: 5px;
      
    }
    .mob-icon{
        display: block;
    }
    .nav li{
        padding: 15px;
    }

    .nav li,.nav .icons,.close{
        display: none;
       
    }

    .nav-open li,.nav-open .icons,.close{
        display: block;
        }
.content{
    height: auto;
    margin-top: 50px;
}

  }